import { fetchUrl, formatDuration, formatTimestampToAMPM, getToken, redirectTo } from "../util";

import {
  XANO_COACH_SIGN_IN_REDIRECT,
  XANO_COACH_DASHBOARD_REDIRECT,
  XANO_COACH_SESSION_SUMMARY_REDIRECT,
  XANO_COACH_SESSION_ATTENDENCE_URL,
} from "../util/xano_urls";

const allUsers = {};
let sessionId, GymId;

window.onload = function () {
 const token = getToken();

  if (token) {
    getSessionData(token);
  } else {
    // ? if there is no token
    redirectTo(`${XANO_COACH_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
  }
};

async function getSessionData(token) {
  var curUrl = new URL(document.location.href);
  sessionId = curUrl.searchParams.get("session"); // ? gets the session id from the url

  if (!sessionId) redirectTo(XANO_COACH_DASHBOARD_REDIRECT);

  document.getElementById("submit-attendence-btn").disabled = true;

  let data = await fetchUrl(XANO_COACH_SESSION_ATTENDENCE_URL + sessionId, "GET", false, token);

  if (data.isFetchOk !== undefined && !data.isFetchOk) {
    console.error(data.message);
    redirectTo(`${XANO_COACH_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    return;
  }
  if (data && data.length > 0) GymId = data[0].gym_id;

  data.some((session) => {
    // ? NOTE: XANO is already checking if the session is the coach's session
    if (session.status === "COMPLETED") {
      // ? is the sesssion already completed?
      console.error("Session is already completed");
      redirectTo(`/coach/session-summary?session=${session.id}`);
      return false;
    } else if (session.status === "IN PROGRESS") {
      // ? is the session in progress?
      appendSessionHeader(session);
      appendAttendence(session, token);
      initSubmitAttendence(session, token);
      return true;
    } else {
      // ? is the session yet to start?
      // ? this else statement also handles any other cases
      console.error("Session either unknown or not in progress");
      redirectTo(`/coach/session-summary?session=${session.id}`);
      return false;
    }
  });
}

function appendSessionHeader(session) {
  const headerContainer = document.getElementById("header-container");

  headerContainer.querySelector("#session-name").innerText = session.session_name;
  headerContainer.querySelector("#time").innerText = formatTimestampToAMPM(session.time, true);
  headerContainer.querySelector("#date").innerText = new Date(session.time).toLocaleDateString("en-us", {
    weekday: "short",
    month: "short",
    day: "numeric",
  });
  headerContainer.querySelector("#gym-location").innerText = session.location;
  headerContainer.querySelector("#duration").innerText = formatDuration(session.duration);
}

function appendAttendence(session) {
  const attendenceContainer = document.getElementById("attendence-container");
  const og_attendence_item = attendenceContainer.querySelector("#attendence-item");

  session.users.forEach((user, indx) => {
    const clonedCard = og_attendence_item.cloneNode(true);
    clonedCard.setAttribute("id", "");

    clonedCard.querySelector("#index").innerText = indx + 1;

    if (user.profile_picture) clonedCard.querySelector("#profile-pic").setAttribute("src", user.profile_picture.url);

    clonedCard.querySelector("#user-name").innerText = user.name;
    clonedCard.querySelector("#user-level").innerText = user.level;
    clonedCard.querySelector("#user-injury").innerText = user.injury;

    const attendence_checkmarks = clonedCard.querySelector("#attendence-checkmarks");
    const didAttend = attendence_checkmarks.querySelector("#did-attend");
    const didNotAttend = attendence_checkmarks.querySelector("#did-not-attend");

    didNotAttend.classList.add("is-true");

    didAttend.onclick = () => {
      didAttend.classList.add("is-true");
      didNotAttend.classList.remove("is-true");

      allUsers[user.user_id] = {
        attended: true,
      };
    };

    didNotAttend.onclick = () => {
      didNotAttend.classList.add("is-true");
      didAttend.classList.remove("is-true");

      allUsers[user.user_id] = {
        attended: false,
      };
    };

    allUsers[user.user_id] = {
      attended: false,
    };

    attendenceContainer.append(clonedCard);
  });

  og_attendence_item.remove();
}

function initSubmitAttendence(session, token) {
  const submitAttendenceBtn = document.getElementById("submit-attendence-btn");
  submitAttendenceBtn.disabled = false;

  submitAttendenceBtn.onclick = async () => {
    submitAttendenceBtn.disabled = true;

    let allUsersAttendence = [];
    Object.keys(allUsers).forEach((userId) =>
      allUsersAttendence.push({ user_id: parseInt(userId), attended: allUsers[userId].attended })
    );

    // ? coach id is already being assigned to the session via its auth id from xano
    // ? therefore, we do not need to add coach_id to the xano_input
    const xano_input = {
      allUsersAttendence,
      gym_id: GymId,
      session_id: parseInt(session.id) || session.id || ""
    };

    // ? TODO: Inside Xano, make sure user is also updating its record -> session_ledger
    const attendenceData = await fetchUrl(XANO_COACH_SESSION_ATTENDENCE_URL + session.id, "POST", xano_input, token);

    if (attendenceData.isFetchOk !== undefined && !attendenceData.isFetchOk) {
      console.error(attendenceData.message);
      redirectTo(`${XANO_COACH_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
      return;
    }

    redirectTo(`${XANO_COACH_SESSION_SUMMARY_REDIRECT}?session=${session.id}`);
  };
}
