import { ROLES } from "./constants";

export function fetchUrl(
  url: string,
  method = "GET",
  body = false,
  token = false,
  hideContentType = false,
  stringifyBody = true
) {
  try {
    let headers: any = { "Content-Type": "application/json" };
    if (hideContentType) headers = {};
    if (token) {
      headers.Authorization = `Bearer ${token}`;
    }
    let options: any = {
      method,
      headers,
    };
    if (body) {
      if (stringifyBody) options.body = JSON.stringify(body);
      else options.body = body;
    }

    return fetch(url, options)
      .then((res) => {
        if (res.ok) {
          return res.json();
        }
        return res.json().then((r) => {
          Promise.reject(r.message);
          return {
            isFetchOk: false,
            status: res.status,
            message: r.message,
            payload: r && r.payload ? r.payload : null,
          };
        });
      })
      .then((json) => json)
      .catch((e) => {
        console.error("Error in fetch", e);
        return e;
        // redirectTo(`${SIGN_IN_URL}?reason=INVALID_TOKEN`);
      });
  } catch (e) {
    console.error("Error!: " + e);
    return e;
  }
}

export function saveToken(token = null, name = "nammax_auth", age = 60 * 60 * 24) {
  if (token)
    // ? expires in 1 day (24 Hours)
    document.cookie = `${name}=${token}; path=/; max-age=${age}`;
}

export function getToken(name = "nammax_auth") {
  let value = `; ${document.cookie}`;
  let parts: any = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop().split(";").shift();
  else return null;
}

export function redirectTo(link = "/", delay = 0) {
  setTimeout(() => {
    window.location.href = link;
  }, delay);
}

export function reloadDelay(delay = 1000) {
  setTimeout(() => {
    window.location.reload();
  }, delay);
}

export function formatTimestampToAMPM(unformattedTime: number | string | Date | null = null, showMinutes = false) {
  if (unformattedTime == null) return "NaN";

  var date = new Date(unformattedTime);
  var hours = date.getHours();
  var ampm = hours >= 12 ? "PM" : "AM";
  hours = hours % 12 || 12; // the hour '0' should be '12'

  if (showMinutes) {
    var minutes: string | number = date.getMinutes();
    minutes = minutes < 10 ? `0${minutes}` : minutes;
    return `${hours}:${minutes} ${ampm}`;
  } else {
    return `${hours} ${ampm}`;
  }
}

export function getDateFromTimestamp(timestamp: number | null = null) {
  if (timestamp == null) return "NaN";

  const date = new Date(timestamp);
  return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
}

export function getTimestampFromDateText(date: string | null = null) {
  if (date == null) return "NaN";

  let df = date.split("/");

  return new Date(`${df[2]}/${df[1]}/${df[0]} ${new Date().getHours()}:${new Date().getMinutes()}`).getTime();
}

export function formatDuration(t: number | null = null) {
  if (t == null) return "NaN";
  t = t * 60;
  let hours = `${(t / 60) ^ 0}`.slice(-2);
  let minutes = ("0" + (t % 60)).slice(-2);

  if (hours != "0") {
    if (minutes != "00") {
      return `${hours} HR ${minutes} M`;
    }
    return hours + " HR";
  }
  return minutes + " Mins";
}

export function calculateAgeFromDOB(dateStr: string | null = null) {
  if (dateStr == null) return "NaN";
  // ? need to split it since new Date() is in the format mm-dd-yyyy not dd-mm-yyyy
  let parts = dateStr.split("-");
  const diff_ms = Date.now() - new Date(parseInt(parts[2]), parseInt(parts[1]) - 1, parseInt(parts[0])).getTime();
  const age_dt = new Date(diff_ms);
  return Math.abs(age_dt.getUTCFullYear() - 1970);
}

export function removeOptions(selectElement: HTMLSelectElement | null, dontRemoveIndx: number | null = null) {
  if (selectElement == null) return;
  var i,
    L = selectElement.options.length - 1;
  for (i = L; i >= 0; i--) {
    if (i !== dontRemoveIndx) selectElement.remove(i);
  }
}

export function logoutBtnOnClick(redirect_url = "/admin/sign-in", element_id = "sidebar-logout-btn") {
  const logoutButton = document.getElementById(element_id);
  if (logoutButton) {
    logoutButton.onclick = () => logout(redirect_url);
  } else {
    console.error("Logout button not found");
  }
}

export function logout(SIGN_IN_URL = "/sign-in") {
  window.localStorage.removeItem("auth");
  redirectTo(`${SIGN_IN_URL}`);
}

export function formatCurrency(amount: string | number = 0) {
  return new Intl.NumberFormat("en-IN", {
    style: "currency",
    currency: "INR",
  })
    .format(Number(amount))
    .replace(/^(\D+)/, "$1 ");
}

// function to automatically get time and update it every minute
export function getAndUpdateTime(element_name = "current-time") {
  const el = document.getElementById(element_name);
  if (el) el.innerText = formatTimestampToAMPM(new Date(), true);
  setTimeout(() => getAndUpdateTime(element_name), 30000); // check every 30 seconds
}

// ? source: https://stackoverflow.com/a/2706169
export function addMonthsToDate(date: Date | null = null, months: number = 0) {
  if (date == null) return "NaN";
  var d = date.getDate();
  date.setMonth(date.getMonth() + +months);
  if (date.getDate() != d) {
    date.setDate(0);
  }
  return date;
}

export function assignAdminNavbar(role = ROLES.ADMIN) {
  if (role === ROLES.ADMIN) {
    const adminNavbar = document.getElementById("admin-nav");
    const superNavbar = document.getElementById("super-admin-nav");
    if (adminNavbar) adminNavbar.style.display = "flex";
    if (superNavbar) superNavbar.remove();
  } else if (role === ROLES.SUPER_ADMIN) {
    const adminNavbar = document.getElementById("admin-nav");
    const superNavbar = document.getElementById("super-admin-nav");
    if (superNavbar) superNavbar.style.display = "flex";
    if (adminNavbar) adminNavbar.remove();
  }
}

export function correctNavbarHref(UrlGymId: string) {
  if (!UrlGymId) return console.error("url does not cotnain gym id");
  const navbarHome = document.getElementById("navbar-home");
  const navbarFinance = document.getElementById("navbar-finance");
  const navbarUsers = document.getElementById("navbar-users");
  const navbarSessions = document.getElementById("navbar-sessions");
  if (navbarHome) navbarHome.setAttribute("href", `/admin/gym/g?gymCode=${UrlGymId}`);
  if (navbarFinance) navbarFinance.setAttribute("href", `/admin/gym/finance?gymCode=${UrlGymId}`);
  if (navbarUsers) navbarUsers.setAttribute("href", `/admin/gym/users?gymCode=${UrlGymId}`);
  if (navbarSessions) navbarSessions.setAttribute("href", `/admin/gym/sessions?gymCode=${UrlGymId}`);
}

export function appendHeader(gym: any) {
  if (!gym) return console.error("gym data not found");
  const gymCodeLocation = document.getElementById("gym-code-location");
  const headerAddress = document.getElementById("header-address");
  const headerPartner = document.getElementById("header-partner");
  const headerContact = document.getElementById("header-contact");
  const headerEmail = document.getElementById("header-email");

  if (gymCodeLocation) gymCodeLocation.innerText = `${gym.code} • ${gym.location}`;
  if (headerAddress) headerAddress.innerText = gym.address;
  if (headerPartner) headerPartner.innerText = gym.partner;
  if (headerContact) headerContact.innerText = gym.contact;
  if (headerEmail) headerEmail.innerText = gym.partner_email;
}

export function removeLoadingScreen() {
  const loadingScreen = document.getElementById("loading-screen");
  if (loadingScreen) loadingScreen.remove();
}

export function errorModal({
  text = "Oops, there was an error",
  error = "Unknown Error",
  callback = null,
  redirectUrl = null,
}) {
  const modal = document.createElement("div");
  modal.style.cssText = `
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000000cc;
  z-index: 99;
`;
  const modalWrapper = document.createElement("div");
  modalWrapper.style.cssText = `
  background-color: white;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: fit-content;
  max-width: 400px;
  max-height: 80%;
  gap: 30px;
  `;

  const top = document.createElement("div");
  top.style.cssText = `
  color: black;
  font-weight: 800;
  text-align: center;
  `;
  top.innerText = text;

  const middle = document.createElement("div");
  middle.style.cssText = `
  color: black;
  background-color: #cccccccc;
  width: 100%;
  min-height: 30px;
  text-align: center;
  padding: 10px;
  `;
  middle.innerText = error;

  const bottom = document.createElement("div");
  bottom.style.cssText = `
  color: #ff0000cc;
  text-align: center;
  margin-bottom: -20px;
  `;
  bottom.innerText = "Please contact an admin for assistance if this issue persists";

  const bottomBtn = document.createElement("button");
  bottomBtn.classList.add("w-button", "small-button");
  bottomBtn.textContent = "RELOAD PAGE";
  bottomBtn.onclick = () => {
    bottomBtn.disabled = true;
    if (redirectUrl) redirectTo(redirectUrl);
    else reloadDelay();
  };

  modalWrapper.appendChild(top);
  modalWrapper.appendChild(middle);
  modalWrapper.appendChild(bottom);
  modalWrapper.appendChild(bottomBtn);
  modal.appendChild(modalWrapper);

  document.body.prepend(modal);
}
